import React, { useState } from 'react';
import { Search, User, FileText, DollarSign, Calculator } from 'lucide-react';

const EMPLOYEES = [
    { id: '1', name: 'Juan Pérez', role: 'QF', salary: 1500000, type: 'Indefinido' },
    { id: '2', name: 'María González', role: 'Caja', salary: 500000, type: 'Plazo Fijo' },
    { id: '3', name: 'Carlos Boss', role: 'Admin', salary: 2500000, type: 'Indefinido' },
];

const HRPage: React.FC = () => {
    const [activeTab, setActiveTab] = useState<'DIRECTORY' | 'SIMULATOR'>('DIRECTORY');
    const [simulatorData, setSimulatorData] = useState({ base: 500000, gratification: 0, commission: 0 });

    const calculateSalary = () => {
        const base = Number(simulatorData.base);
        const grat = Math.min(base * 0.25, 158333); // Tope legal aprox
        const comm = Number(simulatorData.commission);
        const taxable = base + grat + comm;
        const health = taxable * 0.07;
        const pension = taxable * 0.11; // Promedio AFP
        const liquid = taxable - health - pension;
        return { taxable, health, pension, liquid, grat };
    };

    const simulation = calculateSalary();

    return (
        <div className="p-6 bg-slate-50 min-h-screen">
            <header className="mb-8">
                <h1 className="text-3xl font-extrabold text-slate-900">Recursos Humanos</h1>
                <p className="text-slate-500">Gestión de Personal y Remuneraciones</p>
            </header>

            <div className="flex gap-4 mb-8">
                <button
                    onClick={() => setActiveTab('DIRECTORY')}
                    className={`px-6 py-3 rounded-xl font-bold flex items-center gap-2 transition ${activeTab === 'DIRECTORY' ? 'bg-slate-900 text-white shadow-lg' : 'bg-white text-slate-500 hover:bg-slate-100'}`}
                >
                    <User size={20} /> Directorio
                </button>
                <button
                    onClick={() => setActiveTab('SIMULATOR')}
                    className={`px-6 py-3 rounded-xl font-bold flex items-center gap-2 transition ${activeTab === 'SIMULATOR' ? 'bg-slate-900 text-white shadow-lg' : 'bg-white text-slate-500 hover:bg-slate-100'}`}
                >
                    <Calculator size={20} /> Simulador Sueldo
                </button>
            </div>

            {activeTab === 'DIRECTORY' && (
                <div className="bg-white rounded-3xl shadow-sm border border-slate-200 overflow-hidden">
                    <div className="p-6 border-b border-slate-100 flex justify-between items-center">
                        <h2 className="text-xl font-bold text-slate-800">Colaboradores Activos</h2>
                        <div className="relative">
                            <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400" size={18} />
                            <input type="text" placeholder="Buscar..." className="pl-10 pr-4 py-2 bg-slate-50 rounded-lg border border-slate-200 focus:outline-none focus:border-cyan-500" />
                        </div>
                    </div>
                    <table className="w-full text-left">
                        <thead className="bg-slate-50 text-slate-500 font-bold text-sm">
                            <tr>
                                <th className="p-6">Nombre</th>
                                <th className="p-6">Cargo</th>
                                <th className="p-6">Contrato</th>
                                <th className="p-6">Sueldo Base</th>
                                <th className="p-6">Acciones</th>
                            </tr>
                        </thead>
                        <tbody className="divide-y divide-slate-100">
                            {EMPLOYEES.map(emp => (
                                <tr key={emp.id} className="hover:bg-slate-50 transition">
                                    <td className="p-6 font-bold text-slate-700">{emp.name}</td>
                                    <td className="p-6">
                                        <span className="px-3 py-1 bg-blue-50 text-blue-600 rounded-full text-xs font-bold">{emp.role}</span>
                                    </td>
                                    <td className="p-6 text-slate-500">{emp.type}</td>
                                    <td className="p-6 font-mono text-slate-600">${emp.salary.toLocaleString()}</td>
                                    <td className="p-6">
                                        <button className="text-cyan-600 font-bold hover:underline text-sm">Ver Ficha</button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            )}

            {activeTab === 'SIMULATOR' && (
                <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                    <div className="bg-white p-8 rounded-3xl shadow-sm border border-slate-200">
                        <h3 className="text-xl font-bold text-slate-800 mb-6">Parámetros</h3>
                        <div className="space-y-4">
                            <div>
                                <label className="block text-sm font-bold text-slate-700 mb-2">Sueldo Base</label>
                                <input
                                    type="number"
                                    className="w-full p-3 border-2 border-slate-200 rounded-xl focus:border-cyan-500 focus:outline-none"
                                    value={simulatorData.base}
                                    onChange={(e) => setSimulatorData({ ...simulatorData, base: Number(e.target.value) })}
                                />
                            </div>
                            <div>
                                <label className="block text-sm font-bold text-slate-700 mb-2">Comisiones Estimadas</label>
                                <input
                                    type="number"
                                    className="w-full p-3 border-2 border-slate-200 rounded-xl focus:border-cyan-500 focus:outline-none"
                                    value={simulatorData.commission}
                                    onChange={(e) => setSimulatorData({ ...simulatorData, commission: Number(e.target.value) })}
                                />
                            </div>
                        </div>
                    </div>

                    <div className="bg-slate-900 p-8 rounded-3xl text-white shadow-xl">
                        <h3 className="text-xl font-bold mb-6 flex items-center gap-2"><DollarSign /> Resultado Simulado</h3>

                        <div className="space-y-4">
                            <div className="flex justify-between text-slate-400">
                                <span>Sueldo Base</span>
                                <span>${simulatorData.base.toLocaleString()}</span>
                            </div>
                            <div className="flex justify-between text-slate-400">
                                <span>Gratificación (Tope)</span>
                                <span>${Math.round(simulation.grat).toLocaleString()}</span>
                            </div>
                            <div className="flex justify-between text-slate-400">
                                <span>Comisiones</span>
                                <span>${simulatorData.commission.toLocaleString()}</span>
                            </div>
                            <div className="h-px bg-slate-700 my-4" />
                            <div className="flex justify-between text-emerald-400 font-bold text-lg">
                                <span>Total Haberes</span>
                                <span>${Math.round(simulation.taxable).toLocaleString()}</span>
                            </div>
                            <div className="flex justify-between text-red-400 text-sm">
                                <span>Descuentos Legales (~18%)</span>
                                <span>-${Math.round(simulation.health + simulation.pension).toLocaleString()}</span>
                            </div>
                            <div className="h-px bg-slate-700 my-4" />
                            <div className="flex justify-between text-3xl font-extrabold">
                                <span>Líquido a Pagar</span>
                                <span>${Math.round(simulation.liquid).toLocaleString()}</span>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default HRPage;
